.section__project {
   padding: 111px 0 85px;
   @media (max-width: 768px) {
      padding: 60px 0 85px;
   }
}
.project__item-date {
   font-family: "Work Sans";
   font-style: normal;
   font-weight: 600;
   font-size: 18px;
   line-height: 150%;
   color: #403930;
   @media (max-width: 576px) {
      font-size: 16px;
   }
}
.project__item-title {
   line-height: 125% !important;
   color: #14142b;
   .underline {
      width: calc(100%);
      background-image: linear-gradient(transparent calc(100% - 2px), #14142b 2px);
      background-repeat: no-repeat;
      background-size: 0% 100%;
      transition: background-size 1s;
   }
}
.project__item {
   padding: 16px 8px 25px;
   background: #fca61f;
   border-radius: 57px;
   @media (max-width: 450px) {
      border-radius: 20px;
   }
   &:hover .project__item-title .underline {
      background-size: 100% 100%;
   }
}
.project__item:hover .project__item-img img {
   transform: scale(1.05);
   transition: all 0.4s ease;
}
.project__item-img {
   filter: drop-shadow(0px 20px 40px rgba(0, 0, 0, 0.2));
   border-radius: 100px;
   width: 100%;
   height: auto;
   overflow: hidden;
   img {
      width: 100%;
      height: 100%;
      transition: all 0.4s ease;
   }
}
.project__item-content {
   background: #fff;
   border-radius: 57px;
   padding: 20px 25px;
   @media (max-width: 450px) {
      padding: 20px 10px;
      border-radius: 20px;
   }
}
.page-item {
   border: none !important;
   background: none !important;
   box-shadow: none !important;
   &.active {
      .page-link {
         border: none !important;
         background: none !important;
         box-shadow: none !important;
         color: #000000 !important;
      }
   }
   .page-link {
      box-shadow: none !important;
      border: none !important;
      background: none !important;
      font-size: 18px;
      line-height: 21px;
      color: #73737d;
      &.next {
         color: #000000;
         font-weight: 600;
      }
   }
}
.theme-dark {
   .page-item {
      &.active {
         .page-link {
            color: #ffffff !important;
         }
      }
      .page-link {
         color: #8d8d97;
         &.next {
            color: #ffffff;
         }
      }
   }
}
