.section__start{
   padding: 20px 0 66px;
   &-blog{
      .container{
         position: relative;
         .theme-switcher{
            position: absolute;
            right: 0;
            z-index: 99;
            @media (max-width: 576px){
               top: 0;
               right: 20px;
            }
         }
      }
      background: linear-gradient(180deg, #FFF2C5 0%, rgba(250, 250, 250, 0) 100%);
      .start__item.first-item{
         z-index: 2;
         max-width: 652px;
         @media (max-width: 576px){
            margin-top: 40px;
         }
      }
      .start__item.second-item{
         z-index: 0;
         position: relative;
         width: 100%;
         height: 500px;
         @media (max-width: 1200px){
            height: auto;
         }
         img{
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            @media (max-width: 1200px){
               position: relative;
            }
         }
      }
   }
   &-podcast{
      .container{
         position: relative;
         .theme-switcher{
            position: absolute;
            right: 0;
            z-index: 99;
            @media (max-width: 576px){
               top: 0;
               right: 20px;
            }
         } 
      }
      .start__item.first-item{
         @media (max-width: 576px){
            margin-top: 40px;
         }
      }
      .start__item-text{
         font-family: 'Poppins';
         font-style: normal;
         font-weight: 400;
         font-size: 18px;
         line-height: 27px;
         color: #000000;
         max-width: 469px;
      }
   }
}
.start__item-block{
   padding: 12px 24px;
   background: #FFDDBF;
   border-radius: 10px;
   font-family: 'Work Sans';
   color: #403930;
}
.start__item-title{
   font-family: 'Poppins';
   font-style: normal;
   font-weight: 600;
   font-size: 64px;
   line-height: 125%;
   letter-spacing: 0.02em;
   color: #403930;
   @media (max-width: 992px){
      font-size: 48px;
   }
   @media (max-width: 576px){
      font-size: 36px;
   }
   @media (max-width: 400px){
      font-size: 28px;
   }
}
.start__item-btn{
   padding: 24px 54px;
   background: #FCA61F;
   border-radius: 16px;
   font-family: 'Work Sans';
   color: #F3F2FF;
   transition: all .3s ease;
   @media (max-width: 768px){
      padding: 18px 40px ;
   }
   &:hover{
      color: #F3F2FF;
      background: #d8901b;
      transition: all .3s ease;
   }
   &-outline{
      padding: 20px 52px !important;
      @media (max-width: 768px){
         padding: 16px 28px !important;
      }
   }
}  
.start__item.second-item{
   @media (max-width: 1400px){
      width: 100%;
      height: 100%;
      img{
         width: 100%;
         height: 100%;
      }
   }
   @media (max-width: 1200px){
      max-width: 600px;
   }
}
.theme-dark{
   .start__item-btn-outline{
      color: #ffffff;
      border: 4px solid #ffffff;
      &:hover{
         color: #4D5566;
         background: #ffffff;
      }
   }
   .start__item-title, .start__item-text{
      color: #ffffff;
   }
   .section__start-blog{
      background: none;
   }
}