.section__blog{
   padding: 40px 0;
}
.blog__title{
   font-family: 'Merriweather';
   font-style: normal;
   font-weight: 700;
   color: #08080B;
   .underline{
      width: calc(100%);
      background-image: linear-gradient(transparent calc(100% - 2px), #14142B 2px);
      background-repeat: no-repeat;
      background-size: 0% 100%;
      transition: background-size 1s;
   }
}
.blog__text{
   color: #73737D;
   max-width: 515px;
}
.blog__item{
   height: 100%;
   &:hover {
      .blog__title .underline{
         background-size: 100% 100%;
      }
      .blog__item-img img{
         transform: scale(1.05);
         transition: all .4s ease;
      }
   }
}
.blog__body{
   display: none !important;
   opacity: 0;
   transition: all .3s ease;
   &.active{
      display: flex !important;
      opacity: 1;
      transition: all .3s ease;
   }
}
.blog__item-img{
   width: 100%; 
   height: 245px;
   overflow: hidden;
   img{
      width: 100%;
      height: 100%;
      transition: all .4s ease;
   }
}
.blog__mode{
   background: rgba(0, 0, 0, 0.07);
   width: 36px;
   height: 36px;
   border-radius: 50%;
   cursor: pointer;
   path{
      fill: #D6D6D6;
      transition: all .3s ease;
   }
   &.active{
      path{
         fill: #000000;
         transition: all .3s ease;
      }
   }
}
.blog__body-column{
   .blog__item-img{
      width: 100%;
      height: auto;
      img{
         width: 100%;
         height: auto;
      }
   }
   .blog__item-content{
      max-width: 488px;
      width: 100%;
   }
}
.theme-dark{
   .underline{
      background-image: linear-gradient(transparent calc(100% - 2px), #ffffff 2px);
   }
   .blog__mode{
      background: rgba(0, 0, 0, 0.07);
      path{
         fill: #848484;
      }
      &.active{
         path{
            fill: #D6D6D6;
         }
      }
   }
   .blog__text, .blog__title{
      color: #fff;
   }
}