.section__work{
   padding-bottom: 90px;
   img{
      width: 100%;
   }
}
.work__content{
   max-width: 732px;
   width: 100%;
   @media (max-width: 1200px){
     max-width: 500px;
   }
}
.work__title{
   font-family: 'Poppins';
   font-style: normal;
   font-weight: 600;
   font-size: 50px;
   line-height: 125%;
   letter-spacing: 0.02em;
   color: #403930;
   @media (max-width: 1200px){
      font-size: 36px;
   } 
   @media (max-width: 400px){
      font-size: 28px;
   }
}