.section__episodes{
   padding-bottom: 85px;
}
.episodes__title {
   color: #14142b;
}
.episodes__item-title {
   line-height: 125% !important;
   color: #14142b;
}
.episodes__item-img {
   position: relative;
   .img {
      width: 100%;
      height: 100%;
      object-fit: cover;
   }
   .episodes__item-mute {
      position: absolute;
      right: 16px;
      top: 16px;
      background: #facc15;
      border-radius: 27.5px;
      padding: 10px 13px;
   }
}
.episodes__item-link {
   color: #503ae7;
   font-weight: 600;
   .play {
      background: #fca61f;
      border-radius: 32.5926px;
      width: 30px;
      height: 30px;
      img{
         margin: 0 0 5px 3px;
      }
   }
}
.episodes__header{
   max-width: 371px;
   width: 100%;
   margin-bottom: 64px;
}
.theme-dark{
   .episodes__item-title, .episodes__text, .episodes__title{
      color: #fff;
   }
}