.section__contact{
   background: linear-gradient(180deg, #FFF2C5 0%, rgba(250, 250, 250, 0) 100%);
   padding: 103px 0 53px;
   @media (max-width: 768px){
      padding: 60px 0 53px;
   }
}
.contact__subtitle{
   font-family: 'Inter';
   font-style: normal;
   font-weight: 600;
   font-size: 20px;
   line-height: 140%;
   color: #232323;
   @media (max-width: 576px){
      font-size: 18px;
   }
}
.contact__form{
   max-width: 934px;
   width: 100%;
   @media (max-width: 992px){
      max-width: 100%;
   }
}
.contact__form-input, .contact__form-textarea{
   width: 100%;
   color: #232323;
   padding: 15px 24px;
   background: #FFF2C5;
   border: 2px solid #232323;
   @media (max-width: 450px){
      padding: 10px 20px;
   }
   &::placeholder{
      color: #232323;
   }
   &::-moz-placeholder{
      color: #232323;
   }
}
.contact__form-textarea{
   height: 160px;
   resize: none;
}
.contact__item{
   position: relative;
   width: 100%;
   height: auto;
   @media (max-width: 992px){
      height: 600px;
      &-img{
         object-fit: cover;
      }
   }
   @media (max-width: 768px){
      height: 100%;
      &-img{
         object-fit: fill;
      }
   }
   &-img{
      width: 100%;
      height: 100%;
   }
   .contact__content{
      max-width: 352px;
      width: 100%;
      position: absolute;
      right: 0;
      top: 0;
      @media (max-width: 768px){
         max-width: 100%;
         position: relative;
      }
      &-img1{
         position: absolute;
         left: 50%;
         top: 50%;
         transform: translate(-50%, 50%);
         @media (max-width: 1200px){
            height: 100px;
            top: 75%;
         }
         @media (max-width: 768px){
            position: relative;
            left: 0;
            top: 0;
            transform: translate(0);
         }
      }
      &-img2{
         position: absolute;
         right: 0;
         top: 380px;
         @media (max-width: 1200px){
            top: 300px;
            height: 200px;
            right: 50px;
         }
         @media (max-width: 768px){
            position: relative;
            top: 0;
            right: 0;
         }
      }
   }
}
.contact__text{
   font-family: 'Work Sans';
   font-weight: 500;
   color: #000000;
}