.section__info{
   padding: 50px 0 150px;
   @media (max-width: 768px){
      padding: 50px 0 100px;
   }
}
.info__header{
   margin-bottom: 78px;
}
.info__header-img {
   position: relative;
   width: 100%;
   height: 100%;
   .img {
      width: 100%;
      height: 100%;
      object-fit: cover;
   }
   .info__header-mute {
      position: absolute;
      right: 51px;
      top: 21px;
      background: #facc15;
      border-radius: 27.5px;
      padding: 10px 13px;
      @media (max-width: 400px){
         right: 21px;
      }
   }
}
.info__title{
   color: #14142B;
   @media (max-width: 992px){
      font-size: 26px;
   }
}
.info__text{
   &.label{
      color: #503AE7;
      letter-spacing: 3px;
      text-transform: uppercase;
   }
}
.info__btn{
   padding: 16px 32px;
   background: #FAB908;
   font-weight: 500;
   transition: all .3s ease;
   &:hover{
      transition: all .3s ease;
      color: #14142B;
      background: #dda200;
   }
}
.info__content{
   max-width: 636px;
   width: 100%;
}
.info__subtitle{
   color: #14142B;
}
.info__list{
   list-style: disc !important;
   padding-left: 32px !important;
   li{
      opacity: 0.78;
   }
}
.info__author{
   position: relative;
   margin-top: 60px;
   padding-left: 49px;
   @media (max-width: 768px){
      padding-left: 25px;
   }
   @media (max-width: 576px){
      padding-left: 0;
   }
   &::after{
      content: '';
      background: #F2C94C;
      height: 100%; 
      width: 16px;
      position: absolute;
      left: 0;
      top: 0;
      @media (max-width: 576px){
         display: none; 
      }
   }
   .info__text{
      max-width: 320px;
   }
}
.theme-dark{
   .info__title, .info__text, .info__subtitle{
      color: #fff;
   }
   .info__author-icons path{
      fill: #fff;
   }
}