.section__podcast{
   padding: 77px 0 100px;
}
.podcast__title{
   letter-spacing: -1px;
   color: #14142B;
}
.podcast__header{
   margin-bottom: 73px;
}
.podcast__item-title{
   font-family: 'Inter';
   color: #F4F2FF;
}
.podcast__item-text{
   color: #F4F2FF;
}
.podcast__item{
   padding: 64px 40px;
   @media (max-width: 400px){
      width: 100%;
   }
   &.first-item{
      background: #0066D9;
      margin-top: 100px;
      @media (max-width: 1400px){
         margin-top: 0;
      }
   }
   &.second-item{
      background: #36BA54;
   }
   &.third-item{
      background: #FACC15;
      margin-top: 100px;
      @media (max-width: 1400px){
         margin-top: 0;
      }
   }
   &.fourth-item{
      background: #DB3236;
   }
}
.podcast__item-content{
   margin-top: 96px;
   min-width: 225px;
   width: 100%;
   @media (max-width: 576px){
      margin-top: 50px;
   }
   @media (max-width: 400px){
      min-width: fit-content;
   }
}
.theme-dark{
   .podcast__title{
      color: #fff;
   }
}