.section__article{
   padding: 147px 0 100px;
   @media (max-width: 1200px){
      padding: 60px 0 100px;
   }
   @media (max-width: 576px){
      padding: 30px 0 100px;
   }
}
.article__title{
   font-family: 'Merriweather';
   line-height: 1.2;
   color: #000000;
}
.article__date{
   color: #73737D !important;
   span{
      font-weight: 600;
   }
}
.article__text{
   font-size: 18px;
   line-height: 175.6%;
   color: #08080B;
   @media (max-width: 450px){
      font-size: 16px;
   }
   &-bold{
      font-weight: 700;
      a{
         color: #6166DC;
         text-decoration: underline !important;
         &:hover{
            color: #6166DC;
         }
      }
   }
}
.article__subtitle{
   font-family: 'Merriweather';
   font-style: italic;
   font-weight: 700;
   font-size: 36px;
   line-height: 134.82%;
   color: #000000;
   max-width: 723px;
   width: 100%;
   @media (max-width: 768px){
      font-size: 28px;
   }
   @media (max-width: 450px){
      font-size: 24px;
   }
}
.article__subtext{
   color: #000000;
}
.article__content{
   max-width: 810px;
   width: 100%;
}
.article__break{
   width: 100%;
   img{
      width: 100%;
   }
}
.article__swiper{
   height: 453px;
   @media (max-width: 992px){
      height: 400px;
   }
   @media (max-width: 768px){
      height: 350px;
   }
   @media (max-width: 450px){
      height: 270px;
   }
   @media (max-width: 350px){
      height: 220px;
   }
   .swiper-scrollbar{
      width: 5px;
      background: rgba(8, 8, 11, 0.3);
      overflow: hidden;
      height: 100%;
      transition: all .3s ease;
      @media (max-width: 768px){
         display: none;
      }
      &-drag{
         background: #000000;
         transition: all .3s ease;
      }
   }
   .swiper-container{
      border-radius: 72px;
      width: 100%;
      height: 100%;
      .swiper-slide{
         width: 100%;
         height: 100%;
         img{
            width: 100%;
            height: 100%;
         }
      }
   }
}
.article__footer{
   margin-top: 107px;  
}
.article__footer-break{
   color: #73737D;
   position: relative;
   width: 100%;
   &::after{
      content: '';
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate(0, -50%);
      width: 80%;
      height: 1px;
      background: #73737D;
      @media (max-width: 1200px){
         width: 70%;
      }
      @media (max-width: 768px){
         width: 50%;
      }
      @media (max-width: 450px){
         width: 30%;
      }
      @media (max-width: 320px){
         width: 20%;
      }
   }
}
.article__title, .article__subtext, .article__subtitle, .article__text{
   transition: all .3s ease;
}
.prev-btn path{
   transition: all .3s ease;
}
.theme-dark{
   .article__title, .article__subtext, .article__subtitle, .article__text{
      color: #fff;
      transition: all .3s ease;
   }
   .swiper-scrollbar{
      transition: all .3s ease;
      background: rgba(255, 255, 255, 0.3);
      &-drag{
         background: #ffffff;
         transition: all .3s ease;
      }
   }
   .prev-btn path{
      fill: #fff;
      transition: all .3s ease;
   }
}