.section__recent {
   padding-bottom: 86px;
}
.recent__title {
   font-family: "Poppins";
   font-style: normal;
   font-weight: 600;
   font-size: 40px;
   line-height: 125%;
   letter-spacing: 0.02em;
   color: #403930;
   @media (max-width: 768px) {
      font-size: 32px;
   }
}
.recent__link {
   font-family: "Poppins";
   font-style: normal;
   font-weight: 400;
   font-size: 20px;
   line-height: 30px;
   color: #2b2c34;
   img {
      transition: all 0.3s ease;
   }
   &:hover {
      color: #2b2c34;
      img {
         transform: translate(5px, 0);
         transition: all 0.3s ease;
      }
   }
}
.recent__item:hover .recent__item-img img{
   transform: scale(1.05);
   transition: all .4s ease;
}
.recent__item-title {
   font-family: "Work Sans";
   color: #403930;
   @media (max-width: 768px) {
      font-size: 26px; 
   }
}
.recent__item-text {
   font-family: "Work Sans";
   font-weight: 500;
   color: #594f43;
}
.recent__item-link {
   font-family: "Work Sans";
   color: #4e5e80;   
   &.underline {
      width: calc(100%);
      background-image: linear-gradient(transparent calc(100% - 2px), #4e5e80 2px);
      background-repeat: no-repeat;
      background-size: 0% 100%;
      transition: background-size .7s;
   }
   &:hover {
      color: #4e5e80;
      &.underline {
         background-size: 100% 100%;
      }
   }
}
.recent__item-content {
   max-width: 404px;
   width: 100%;
}
.recent__item-img {
   width: 100%;
   height: auto;
   overflow: hidden;
   img{
      width: 100%;
      height: 100%;
      transition: all .4s ease;
   }
}
