.section__about{
   padding: 103px 0 52px;
   @media (max-width: 768px){
      padding: 60px 0 52px;
   }
   img{
      width: 100%; 
   }
}
.about__text{
   font-family: 'Poppins';
   font-style: normal;
   font-weight: 400;
   font-size: 25px;
   line-height: 38px;
   color: #232323;
   @media (max-width: 992px){
      font-size: 20px;
      line-height: 1.4;
   }
   @media (max-width: 400px){
      font-size: 18px;
   }
}