.section__feature{
   background: linear-gradient(180deg, #FFFFFE 0%, rgba(255, 255, 255, 0) 100%);
   padding-bottom: 173px;
   @media (max-width: 768px){
      padding-bottom: 100px;
   }
}
.feature__item-block{
   background: #F9FAFB;
   border-radius: 29px;
   font-family: 'Inter';
   color: #0F766E;
   padding: 13px 21px;
}
.feature__item-title{ 
   font-family: 'Inter';
   font-style: normal;
   font-weight: 600;
   font-size: 28px;
   line-height: 30px;
   color: #111827;
}
.feature__item-text{
   font-family: 'Inter';
   color: #648E9E;
   max-width: 364px;
   width: 100%;
   a{
      color: #648E9E;
   }
}
.feature__item-content{
   position: absolute;
   bottom: 38px;
   @media (max-width: 768px){
      position: relative;
      bottom: 0;
   }
}
.feature__item{
   border-radius: 28px;
   padding: 32px 44px !important;
   position: relative;
   min-height: 402px;
   height: 100%;
   @media (max-width: 768px){
      min-height: fit-content;
   }
   @media (max-width: 576px){
      padding: 20px 15px !important;
   }
   &.first-item{
      background: linear-gradient(109.56deg, #CCFBF1 8.75%, #BBDCE9 101.85%, #99F6E4 101.85%);
      .feature__item-images{
         position: absolute;
         top: 32px;
         left: 300px;
         max-width: 400px;
         height: 100%;
         @media (max-width: 1400px){
            left: 270px;
         }
         @media (max-width: 1200px){
            left: 170px;
            top: 0px;
         }
         @media (max-width: 992px){
            top: 32px;
            right: 0 !important;
            left: unset;
            max-width: none;
            width: 100%;
         }
         @media (max-width: 768px){
            position: relative;
         }
         @media (max-width: 400px){
            flex-direction: column !important;
            picture{
               display: flex;
               justify-content: center;
            }
         }  
         .first-img{
            position: absolute;
            top: -30px;
            left: 0px;
            @media (max-width: 992px){
               right: 222px;
               left: auto;
            }
            @media (max-width: 768px){
               position: relative;
               right: 0;
            }
            @media (max-width: 400px){
               width: 80%;
            } 
         }
         .second-img{
            position: absolute;
            bottom: 50px;
            left: 180px;
            @media (max-width: 1200px){
               top: -30px;
            }
            @media (max-width: 992px){
               right: 32px;
               left: auto;
            }
            @media (max-width: 768px){
               position: relative;
               right: 0;
            }
            @media (max-width: 400px){
               width: 80%;
            } 
         }
      }
   }
   &.second-item{
      background: linear-gradient(128.14deg, #FFEDD5 2.71%, #FED7AA 97.55%);
      .feature__item-text{
         color: #C07E63;
      }
      .images__item{
         background: #F9FAFB;
         border-radius: 50%;
         &.small-item{
            width: 77px;
            height: 77px;
            @media (max-width: 1200px){
               width: 50px;
               height: 50px;
            }
            img{
               width: 28px;
               height: 28px;
            }
         }
         &.big-item{
            width: 133px;
            height: 133px;
            @media (max-width: 1200px){
               width: 100px;
               height: 100px;
            }
            img{
               width: 48px;
               height: 48px;
            }
         }
      }
   }
   &.third-item {
      background: linear-gradient(127.16deg, #FEF9C3 3.85%, #FEF08A 95.73%);
      .feature__item-image{
         width: 147px;
         height: 147px;
         border-radius: 50%;
         background: #F9FAFB;
         img{
            width: 48px;
            height: 48px;
         }
      }
      .feature__item-text{
         color: #B49467;
      }
   }
   &.fourth-item{
      background: linear-gradient(109.56deg, #F3E8FF 8.75%, #D8B4FE 101.85%);
      .feature__item-text{
         color: #746FA7;
      }
      .feature__item-img{
         position: relative;
         height: 84px;
         @media (max-width: 576px){
            height: auto;
            .first-img, .second-img, .third-img, .fourth-img, .fifth-img, .sixth-img, .seventh-img{
               position: relative;
               left: 0;
            }
         }
         .first-img{
            position: absolute;
            top: 0;
            left: 0;
         }
         .second-img{
            position: absolute;
            top: 0;
            left: 50px;
         }
         .third-img{
            position: absolute;
            top: 0;
            left: 0;
         }
         .fourth-img{
            position: absolute;
            top: 0;
            left: 50px;
            @media (max-width: 1200px){
               left: 40px;
            }
         }
         .fifth-img{
            position: absolute;
            top: 0;
            left: 100px;
            @media (max-width: 1200px){
               left: 90px;
            }
         }
         .sixth-img{
            position: absolute;
            top: 0;
            left: 150px;
            @media (max-width: 1200px){
               left: 140px;
            }
         }
         .seventh-img{
            position: absolute;
            top: 0;
            left: 200px;
            @media (max-width: 1200px){
               left: 190px;
            }
         }
      }
   }
   &.fifth-item{
      background: linear-gradient(180deg, #DDD6FE 100%, #A78BFA 0%);
      .feature__item-title{
         font-family: 'Inter';
         font-style: normal;
         font-weight: 600;
         font-size: 40px;
         line-height: 30px;
         color: #111827;
      }
      .feature__item-text{
         color: #9E69AA;
      }
      .feature__item-btn{
         padding: 20px 36px;
         background: linear-gradient(93.54deg, #AF51C5 5.09%, #6E297E 91.93%);
         border-radius: 5px;
         color: #F9FAFB; 
         font-weight: 500;
         text-transform: uppercase;
      }
      .feature__item-content{
         position: relative;
         bottom: 0;
         &::after{
            content: '';
            position: absolute;
            right: -200px;
            top: -200px;
            width: 183px;
            height: 186px;
            background: #A222C0;
            filter: blur(200px);
            border-radius: 50%;
         }
      }
      img{
         width: 100%;
      }
   }
}
