.footer__title{
   font-family: 'Work Sans';
   color: #403930;
   padding: 12px 24px;
   background: #FFDDBF;
   border-radius: 10px;
   position: relative;
   left: 50%;
   transform: translate(-50%, 0);
} 
.footer__icons a{
   transition: all .3s ease;
   @media (max-width: 768px){
      img{
         width: 60px;
         height: auto;
      }
   }
   &:hover{
      transition: all .3s ease;
      transform: rotate(15deg);
   }
}
.footer__copy{
   font-family: 'Poppins';
   color: #2B2C34;
}
.theme-dark{
   .footer__copy{
      color: #fff;
      transition: all .3s ease;
   }
}
