.nav-link {
   font-family: "Poppins";
   font-style: normal;
   font-weight: 700;
   font-size: 20px;
   line-height: 30px;
   text-transform: capitalize;
   color: #2b2c34 !important;
   transition: all 0.3s ease;
   &.active {
      border-radius: 16px;
      padding: 10px 25px !important;
      background: #fca61f;
      color: #fffffe !important;
      &:hover {
         color: #fffffe;
         background: #d58d18;
         transition: all 0.3s ease;
         opacity: 1;
      }
   }
   &:hover {
      transition: all 0.3s ease;
      opacity: 0.8;
   }
}
.nav-btn {
   padding: 10px 17px;
   font-family: "Poppins";
   font-style: normal;
   font-weight: 700;
   font-size: 18px;
   line-height: 27px;
   text-transform: capitalize;
   color: #fffffe;
   background: #fca61f;
   border-radius: 4px;
   margin-left: 30px;
   transition: all 0.3s ease;
   @media (max-width: 1200px) {
      margin-left: 0;
   }
   &:hover {
      color: #fffffe;
      background: #d58d18;
      transition: all 0.3s ease;
   }
}
.navbar-toggler {
   color: none !important;
   border: none !important;
   background: none !important;
   @media (min-width: 1200px) {
      display: none !important;
   }
   @media (max-width: 1200px) {
      display: block !important;
   }
   &:focus {
      box-shadow: none !important;
   }
   .navbar-toggler-icon {
      position: relative;
      height: 20px;
      width: 30px;
      background: none !important;
      &::before,
      &::after {
         content: "";
         position: absolute;
         height: 2px;
         width: 100%;
         right: 0;
         background-color: #fca61f;
         transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
      }
      &::before {
         top: 0;
         transform: rotate(0deg);
      }
      &::after {
         bottom: 0;
         transform: rotate(0deg);
      }
      span {
         position: absolute;
         top: 9px;
         right: 0;
         width: 100%;
         background-color: #fca61f;
         height: 2px;
         transform: scale(1);
         transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
      }
   }
   &.active {
      .navbar-toggler-icon {
         position: relative;
         height: 20px;
         width: 30px;
         background: none !important;
         &::before,
         &::after {
            content: "";
            position: absolute;
            height: 2px;
            width: 100%;
            right: 0;
            background-color: #fca61f;
         }
         &::before {
            transform: rotate(45deg);
            transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
            top: 8px;
         }
         &::after {
            transform: rotate(-45deg);
            transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
            bottom: 10px;
         }
         & span {
            transform: scale(0);
            position: absolute;
            right: 0;
            width: 100%;
            background-color: #fca61f;
            height: 2px;
            transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
         }
      }
   }
}
.navbar {
   padding: 31px 0;
   @media (max-width: 1200px) {
      animation: fade 0.3s ease both;
      padding: 0;
      &.active {
         width: 100vw;
         height: 100vh;
         position: fixed;
         left: 0;
         top: 0;
         bottom: 0;
         right: 0;
         z-index: 999;
         background-color: #ffffff;
         overflow-y: scroll;
         &::-webkit-scrollbar {
            display: none;
         }
         .container {
            position: relative !important;
            height: 100%;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 50px;
         }
         .navbar-brand {
            position: absolute;
            top: 0;
            left: 0.75em;
         }
         .navbar-toggler {
            position: absolute;
            right: 0.75em;
            top: 20px;
         }
      }
   }
}
.nav {
   @media (max-width: 1200px) {
      display: none !important;
      &.active {
         display: flex !important;
      }
   }
   @media (max-width: 1200px) and (max-height: 600px) {
      margin-top: 100px;
      height: 600px;
      overflow-y: scroll !important;
      &::-webkit-scrollbar {
         display: none;
      }
   }
}
.theme-dark {
   .navbar {
      @media (max-width: 1200px) {
         &.active {
            background-color: #000000;
         }
      }
   }
   .nav-link {
      color: #fff !important;
   }
   .navbar-brand {
      .path1 {
         fill: #fffffe;
         transition: all 0.3s ease;
      }
      .path2 {
         fill: #9084cf;
         transition: all 0.3s ease;
      }
   }
}
