// mixins
@mixin font-face($font_name, $file_name, $weight) {
   @font-face {
      font-family: font_name;
      font-display: swap;
      src: url("../fonts/#{file_name}.woff") format("woff"), url("../fonts/#{file_name}.woff2") format("woff2");
      font-weight: #{weight};
   }
}
body { 
   overflow-x: hidden !important;
   transition: all 0.3s ease;
   display: flex;
   flex-direction: column;
   min-height: 100vh;
   height: 100%;
}
h1,
h2,
h3,
h4,
h5,
h6,
span,
li,
ul,
ol,
p {
   margin: 0 !important;
   padding: 0 !important;
   transition: all 0.3s ease;
}
div,
section {
   transition: all 0.3s ease;
}
a {
   text-decoration: none !important;
}
.section {
   overflow-x: hidden !important;
}
.theme__path {
   transition: all 0.3s ease;
}
.main {
   flex: 1 1 auto;
   background: linear-gradient(180deg, #fff2c5 0%, rgba(250, 250, 250, 0) 100%);
   backdrop-filter: blur(250px);
   transition: all 0.3s ease;
   @media (max-height: 760px) {
      background-color: #fff;
      backdrop-filter: none;
   }
}
//main
.theme-dark {
   transition: all 0.3s ease;
   background-color: #000;
   color: #fff !important;
   .theme__path {
      fill: #fff;
      transition: all 0.3s ease;
   }
   .main {
      background: none;
      backdrop-filter: none;
   }
   .theme-switcher {
      transform: rotate(180deg);
      background-color: #fff;
      border-radius: 50%;
   }
}
.theme-switcher {
   cursor: pointer;
}
.page__title {
   font-family: "Poppins";
   font-style: normal;
   font-weight: 800;
   font-size: 80px;
   line-height: 120px;
   color: #000000;
   @media (max-width: 992px) {
      font-size: 64px;
      line-height: 1.4;
   }
   @media (max-width: 768px) {
      font-size: 48px;
   }
   @media (max-width: 400px) {
      font-size: 36px;
   }
}
.page__subtitle {
   font-style: normal;
   font-weight: 700;
   font-size: 48px;
   line-height: 1.2;
   @media (max-width: 768px) {
      font-size: 36px;
   }
   @media (max-width: 450px) {
      font-size: 28px;
   }
}
.page__text {
   font-style: normal;
   font-weight: 400;
   font-size: 14px;
   line-height: 24px;
}
.page__subtext {
   font-family: "Inter";
   font-style: normal;
   font-weight: 400;
   font-size: 16px;
   line-height: 150%;
   color: #14142b;
}
.section__title {
   font-family: "Inter";
   font-style: normal;
   font-weight: 600;
   font-size: 24px;
   line-height: 1.5;
   @media (max-width: 768px) {
      font-size: 20px;
   }
}
.section__subtitle {
   font-family: "Inter";
   font-style: normal;
   font-weight: 700;
   font-size: 32px;
   line-height: 120%;
}
.section__btn-outline {
   font-family: 'Work Sans';
   color: #4D5566;
   border: 4px solid #4D5566; 
   border-radius: 16px;
   padding: 24px 66px;
   transition: all .3s ease;
   background: none;
   @media (max-width: 450px){
      padding: 18px 34px;
   }
   &:hover {
      color: #ffffff;
      background: #4d5566;
      transition: all 0.3s ease;
   }
}
// keyframes
@keyframes fade {
   0% {
      opacity: 0;
   }
   100% {
      opacity: 1;
   }
}