.section__updates{
   padding-bottom: 69px;
}
.updates__title{
   font-weight: 700;
   font-size: 36px;
   line-height: 45px;
   text-transform: capitalize;
   color: #2B2C34;
   @media (max-width: 768px){
      font-size: 30px;
      line-height: 1.5;
   }
}
.updates__text{
   font-size: 20px;
   line-height: 25px;
   color: #4D5566;
   @media (max-width: 768px){
      font-size: 18px;
      line-height: 1.5;
   }
}
.updates__form{
   width: 100%;
}
.updates__form-input{
   background: #FFFFFE;
   border: 2px solid #4D5566;
   border-radius: 8px;
   padding: 21px 25px;
   max-width: 444px;
   width: 100%;
   transition: all .3s ease;
   @media (max-width: 576px){
      max-width: 100%;
   }
   &:focus-visible{
      outline: none !important;
      transition: all .3s ease;
      box-shadow: 0 4px 5px #4D5566;
      border: 1px solid #4D5566;
   }
}
.updates__form-btn{
   background: #F2C94C;
   border-radius: 8px;
   border: none;
   padding: 21px 25px;
   color: #FFFFFE;
   transition: all .3s ease;
   &:hover{
      background: #d0ac3e;
      transition: all .3s ease;
   }
}
.theme-dark{
   .updates__title, .updates__text{
      color: #fff;
   }
   .updates__form-input{
      color: #4D5566;
      &:focus-visible{
         box-shadow: 0 4px 5px #ffffff;
         border: 1px solid #ffffff;
      }
   } 
}